import Clipboard from './clipboard';
const install = function (Vue) {
	Vue.directive('Clipboard', Clipboard); // 
};
if (window.Vue) {
	window.clipboard = Clipboard;
	//  Vue.use() 
	Vue.use(install);
}
//  install
Clipboard.install = install;
export default Clipboard;



