const Clipboard = require('Clipboard');
if (!Clipboard) {
	throw new Error('you shold npm install `clipboard` --save at first ');
}
export default {
    /* 
        el DOM,---> el 
        binding
        name v- 
        valuev-my-directive="1 + 1"  2
        oldValue update  componentUpdated 
        expression v-my-directive="1 + 1"  "1 + 1"
        arg v-my-directive:foo  "foo"
        modifiersv-my-directive.foo.bar  { foo: true, bar: true }
        vnodeVue  VNode API 
        oldVnode update  componentUpdated 
    */
	bind(el, binding) {
		if (binding.arg === 'success') {
			el._v_clipboard_success = binding.value;
		} else if (binding.arg === 'error') {
			el._v_clipboard_error = binding.value;
             console.log(el, el._v_clipboard_error);
		} else {
			const clipboard = new Clipboard(el, {
			text() { return binding.value; },
			action() { return binding.arg === 'cut' ? 'cut' : 'copy'; }
        });
        clipboard.on('success', e => {
			const callback = el._v_clipboard_success;
			callback && callback(e); // eslint-disable-line
        });
        clipboard.on('error', e => {
			const callback = el._v_clipboard_error;
			callback && callback(e); // eslint-disable-line
        });
          el._v_clipboard = clipboard;
      }
	},
	update(el, binding) {
		if (binding.arg === 'success') {
			el._v_clipboard_success = binding.value;
		} else if (binding.arg === 'error') {
			el._v_clipboard_error = binding.value;
		} else {
            el._v_clipboard.text = function() { return binding.value; };
            el._v_clipboard.action = function() { return binding.arg === 'cut' ? 'cut' : 'copy'; };
		}
	},
	unbind(el, binding) {
		if (binding.arg === 'success') {
			delete el._v_clipboard_success;
		} else if (binding.arg === 'error') {
			delete el._v_clipboard_error;
		} else {
			el._v_clipboard.destroy();
			delete el._v_clipboard;
		}
  }
};



