
<template>
  <div id="app">
   <v-header :seller='seller'></v-header>
    <div class="tab border-1px">
    	<div class="tab-item">
          <router-link to='/goods'></router-link>
        </div>
        <div class="tab-item">
            <router-link to='/ratings'></router-link>
        </div>
        <div class="tab-item">
            <router-link to='/seller'></router-link>
        </div>
    </div>
   <router-view :seller='seller'></router-view>
  </div>
</template>

<script>
/* export default   new Vue() data,   export default methods, data, computed, , new Vue() data  .vue , data return */
    import sellheader from './components/header/header';

    const ERR_OK = 0;

    export default {
      data() {
        return {
          seller: {
            type: Object
          }
        };
      },
      created() { // ajax
        this.$http.get('/api/seller').then((response) => {
          response = response.body;
          // console.log(response);
          if (response.errno === ERR_OK) {
            this.seller = response.data;
            console.log(this.seller);
          }
        });
      },
        components: {
           'v-header': sellheader
        }
    };
</script>

<style lang='less'> 
@import url("./common/css/same.less");
  #app .tab {
    display: flex;
    width: 100%;
    height: 40px;
    line-height: 40px;
    // border-bottom: 1px solid rgba(7,17,27,0.1);
    .border-1px(rgba(7,17,27,0.1));
  .tab-item {
    flex:1;text-align: center;
      & > a {
    display: block;
    font-size: 14px;
    color:rgb(77,85,93);
     &.active {
    color:rgb(240,20,20);
  }
  }
}
}
</style>



